	module.exports = {"data":{"markdownRemark":{"html":"<p>HackerOne integrates with many issue tracking tools. The integration ensures that the HackerOne platform fits into your existing security workflow with minimal friction. Integrating with one of these services will enable you to push report submissions into your preferred systems. </p>\n<p>HackerOne currently supports these integrations:</p>\n<ul>\n<li>Jira </li>\n<li>Slack </li>\n<li>Assembla</li>\n<li>Bugzilla</li>\n<li>Freshdesk</li>\n<li>GitHub</li>\n<li>GitLab</li>\n<li>MantisBT</li>\n<li>OTRS</li>\n<li>Phabricator </li>\n<li>ServiceNow</li>\n<li>Redmine</li>\n<li>Trac</li>\n<li>Zendesk</li>\n</ul>\n<p>Don't see your issue tracker listed here? <a href=\"https://support.hackerone.com/hc/en-us/requests/new\">Let HackerOne know</a> to get that added for you.</p>","frontmatter":{"path":"/programs/supported-integrations","title":"Supported Integrations"}}},"pathContext":{}}

